.intro{
  &-wrapper{
    position: relative;
    padding: 90vw 0 85vw;
  }

  .title{
    position: absolute;
    top: 25%;
    left: 0;
    width: 100%;

    .line{
      display: flex;
      align-items: center;
      transform-style: preserve-3d;
      perspective: 150px;

      span{
        display: inline-block;
        transform: rotateX(-120deg);
        opacity: 0;
        transform-style: preserve-3d;
        font-size: 10.4vw;
        font-family: 'LeferiPoint-WhiteObliqueA';
        transition: transform 1.4s, ease-in-out opacity 1.2s ease-in-out;
      }

      &:nth-child(2){
        padding-left: 30%;
      }
    }

    &.active{
      .line{
        &:nth-child(1){
          @for $i from 1 through 12{
            span:nth-child(#{$i}){
              transition-duration: calc(1.25s + #{0.08 * $i}s);
            }
          }
        }

        &:nth-child(2){
          span{
            font-family: 'LeferiPoint-SpecialItalicA';
          }

          @for $i from 1 through 9{
            span:nth-child(#{$i}){
              transition-delay: 400ms;
              transition-duration: calc(1.25s + #{0.08 * $i}s);
            }
          }
        }

        &:nth-child(3){
          @for $i from 1 through 9{
            span:nth-child(#{$i}){
              transition-delay: 700ms;
              transition-duration: calc(1.25s + #{0.08 * $i}s);
            }
          }
        }

        span{
          transform:rotateX(0);
          opacity: 1;
        }
      }
    }

    &#fixed{
      position: fixed;
      z-index: 50;
      padding: 0 20px;
      left: 0;
      top: 0;
      width: 100%;
    }
  }

  p{
    text-align: center;
    font-size: 1.6rem;
    color: #444;
    opacity: 0;
    transform: translate3d(0,10px,0);
    transition: transform 1.2s ease-in-out, opacity 1.2s ease-in-out;
    transition-delay: 1s;
    font-family: $font-para;
  }
  
  video{
    position: absolute;
    left: 0;
    bottom: 2%;
  }

  @media screen and (min-width: $md-breakpoint) {
    height: calc(100vh - 90px);
    
    &-wrapper{
      padding: 75vh 0 10px;
    }

    .title{
      top: 20vh;

      .line {
        perspective: 500px;

        span{
          font-size: 8vw;
        }

        &:nth-child(2){
          padding-left: 0;
        }
      }


    }

    p{
      text-align: left;
      font-size: 1.8rem;
    }
  }

  @media screen and (min-width: $lg-breakpoint) {
    &-wrapper{
    }

    .title{
      top: 21%;

      .line span{
        font-size: 6vw;
      }
    }

    p{
      position: absolute;
      top: 26.8vw;
      left: 19vw;
    }
  }
}