.header{
  top: 0;
  z-index: 50;
  padding: 20px 0;
  width: 100%;

  &-wrapper{
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-top: 20px;
  }

  .main-logo{
    font-size: 1.6rem;
    font-family: 'Roboto Condensed', sans-serif;
  }

  .menu-btn{
    display: inline-block;
    width: 60px;
    height: 25px;
    padding-top: 5px;
    font-size: 1.4rem;
    color: #f0f0f0;
    background-color: #111;
    border-radius: 15px;
    font-family: 'LeferiPoint-WhiteA';
  }

  &::before{
    position: absolute;
    top: 20px;
    right: 0;
    width: calc(100% - 20px);
    height: 1px;
    background-color: #111;
    content: '';
  }

  .sidebar{
    position: fixed;
    z-index: 100;
    top: 85px;
    right: 0;
    padding: 50px 0;
    width: 100%;
    height: calc(100vh - 85px);
    transform: translate3D(100%,0,0);
    background-color: $background;
    transition: transform 300ms ease-in-out;
    
    .snb{
      &-item{
        line-height: 1.8;
        font-size: 7vw;
      }
    }
  
    &.active{
      transform: translate3D(0,0,0);
    }
  }

  @media screen and (min-width: $md-breakpoint) {
    .main-logo{
      font-size: 2.4rem;
    }

    .menu-btn{
      width: 80px;
      height: 30px;
      font-size: 1.8rem;
    }
  }
}