.project{
  padding: 90px 0;

  &-item{
    position: relative;
    overflow: hidden;

    &::after{
      position: absolute;
      left: 0;
      bottom: 0;
      width: 100%;
      height: 1px;
      background-color: #a1a1a1;
      content: '';
    }

    a{
      display: block;
      padding: 6.3vw 0 3vw;;

      &:hover .rotate-box{
        transform: translate3d(0,0,0);
      }
    }
  }

  h2{
    position: relative;
    padding-bottom: 1.25em;
    font-size: 1.6rem;
    font-family: $font-tit;

    &::before{
      position: absolute;
      left: 0;
      bottom: 0;
      width: 100%;
      height: 1px;
      background-color: #111;
      content: '';
    }
  }

  .rotate-box{
    transform: translate3d(8%,0,0);
    transition: transform 300ms ease-in-out;

    h3{
      width: 60%;
      font-size: 11vw;
      letter-spacing: -0.03em;
      font-weight: 100;
    }
  }

  @media screen and (min-width: $md-breakpoint){
    .rotate-box{
      transform: translate3d(10%, 0, 0);

      h3{
        font-size: 8vw;
      }
    }
  }
}

@media screen and (min-width: $lg-breakpoint){
  .project .inner{
    padding: 0 10%;
  }
  .project .project-item {
    position: relative;
    overflow: hidden;
  }
  .project .project-item::after {
    position: absolute;
    left: 0;
    bottom: 0;
    width: 100%;
    height: 0.08vw;
    background-color: #111;
    content: '';
  }
  .project .project-item:last-child:after {
    display: none;
  }
  .project .project-item a {
    display: block;
    padding: 7vw 0 2vw;
    width: 100%;
    height: 12.45vw;
    transform-style: preserve-3d;
    box-sizing: content-box;
  }
  .project .project-item a:hover .rotate-box {
    transform: rotateX(90deg);
  }
  .project .rotate-box {
    position: relative;
    height: 170px;
    height: 12.45vw;
    transition: transform 800ms;
    transform-style: preserve-3d;
    transform: translate3D(7%, 0, -85px);
  }
  .project .rotate-box .front {
    height: 12.45vw;
    transform: rotateX(0) translateZ(85px);
  }
  .project .rotate-box .bottom {
    height: 12.45vw;
    padding: 40px 0;
    transform: rotateX(-90deg) translateZ(85px);
  }
  .project .rotate-box .box {
    position: absolute;
    width: 100%;
    height: 100%;
  }
  .project .rotate-box .box h3 {
    width: 40%;
    padding-left: 35px;
    line-height: 0.91em;
    font-size: 5vw;
    font-weight: 300;
  }
  .project .rotate-box .box em {
    font-size: 1.17vw;
  }
  .project .rotate-box .front em {
    position: absolute;
    left: 0;
    top: 8px;
  }
  .project .rotate-box .bottom em {
    position: absolute;
    left: 0;
    top: 48px;
  }
  .project .rotate-box .bottom .project-infor {
    display: inline-block;
    position: relative;
  }
  .project .rotate-box .bottom .project-infor h3 {
    margin-right: 20px;
  }
  .project .rotate-box .bottom .detail-list {
    position: absolute;
    left: 100%;
    top: 50%;
    padding-left: 3.3vw;
    width: 100%;
    font-size: 1.17vw;
    transform: translateY(-50%);
    font-family: 'LeferiPoint-WhiteObliqueA';
  }
  .project .rotate-box .bottom .detail-list::before {
    position: absolute;
    width: 2vw;
    height: 4.8vw;
    left: 0;
    top: 50%;
    transform: translateY(-50%);
    // background: url('../images/line.svg') no-repeat center;
    background-size: contain;
    content: '';
  }
}